<template>
  <div>
    <div id="top_view">
      <el-row>
        <el-col :span="12">
          <h3>发布人基本信息</h3>
        </el-col>
        <el-col :span="12"
                style="text-align: right">
          <!-- <span >{{ status }}</span> -->
          <el-image style="width: 80px; height: 80px; object-fit:cover"
                    :src="statusImg"
                    fit="scale-down"
                    @click="modifyPortraitDialogVisible = true">
          </el-image>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <el-col :span="10">
            <span>发布人：</span>
          </el-col>
          <span>{{ info.publisher_name }}</span>
        </el-col>
        <el-col :span="24">
          <el-col :span="10">
            <span>发布手机号：</span>
          </el-col>
          <span>{{ info.publisher_phone }}</span>
        </el-col>
      </el-row>
    </div>
    <div id="middle_view">
      <h3>发布需求信息</h3>
      <!-- 第一行开始 -->
      <!-- 母婴护理师 -->
      <el-row v-if="parseInt(info.type_id) === 1">
        <el-col :span="24">
          <label for>预 产 期 ：</label>
          <span>{{ info.due_time }}</span>
        </el-col>
        <el-col :span="24">
          <label for>服务期限：</label>
          <span>{{ info.service_date ? info.service_date.name : '-' }}</span>
        </el-col>
        <el-col :span="24">
          <label for>宝贝数量：</label>
          <span>{{ info.baby_num ? info.baby_num.name : '-' }}</span>
        </el-col>
      </el-row>
      <!-- 家政工 -->
      <el-row v-if="parseInt(info.type_id) === 2">
        <el-col :span="24">
          <label for>服务期限：</label>
          <label for>12月</label>
        </el-col>
        <el-col :span="24">
          <label for>服务方式：</label>
          <span>{{ info.live_home ? info.live_home.name : '-' }}</span>
        </el-col>
      </el-row>
      <!-- 护工 -->
      <el-row v-if="parseInt(info.type_id) === 4">
        <el-col :span="24">
          <label for>服务期限：</label>
          <label for>12月</label>
        </el-col>
        <el-col :span="24">
          <label for>服务方式：</label>
          <span>{{ info.live_home ? info.live_home.name : '-' }}</span>
        </el-col>
        <el-col :span="24">
          <label for>服务对象：</label>
          <span>{{ info.nursin_status ? info.nursin_status.name : '-' }}</span>
        </el-col>
      </el-row>
      <!-- 育儿嫂 -->
      <el-row v-if="parseInt(info.type_id) === 3">
        <el-col :span="24">
          <label for>服务期限：</label>
          <label for>12月</label>
        </el-col>
        <el-col :span="24">
          <label for>宝宝数量：</label>
          <span>{{ info.baby_num ? info.baby_num.name : '-' }}</span>
        </el-col>
        <el-col :span="24">
          <label for>宝宝年龄：</label>
          <span>{{ info.baby_age ? info.baby_age.name : '-' }}</span>
        </el-col>
        <el-col :span="24">
          <label for>服务方式：</label>
          <span>{{ info.live_home ? info.live_home.name : '-' }}</span>
        </el-col>
      </el-row>
      <!-- 第一行结束 -->
      <!-- 第二行开始 -->
      <el-row style="margin-top: 10px;">
        <el-col :span="24">
          <label for>服务薪资：</label>
          <span>{{ info.service_salary ? info.service_salary.salary : '-' }}</span>
        </el-col>
        <el-col :span="24">
          <label for>经验要求：</label>
          <span>{{ info.experience_id ? info.experience_id.name : '-' }}</span>
        </el-col>
        <el-col :span="24">
          <label for>年龄要求：</label>
          <span>{{ info.service_age ? info.service_age.name : '-' }}</span>
        </el-col>
      </el-row>
      <!-- 第二行结束 -->
      <!-- 第三行开始 -->
      <el-row style="margin-top: 10px;">
        <el-col :span="24">
          <div style="display: flex;">
            <label for>服务地址：</label>
            <el-input disabled
                      style="flex: 1;"
                      type="textarea"
                      rows="8"
                      resize="none"
                      maxlength="500"
                      v-model="address"></el-input>
          </div>
        </el-col>
      </el-row>
      <!-- 第三行结束 -->
    </div>
    <div id="bottom_view">
      <div class="new">
        <div id="name"
             class="form-group">
          <span>籍贯要求：</span>
          <div class="bao">
            <div class="native">
              <div v-for="(val, index) in region"
                   v-bind:key="index"
                   :id="val.code">{{val.name}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="new">
        <span>技能要求：</span>
        <div class="bao">
          <div class="native">
            <div v-for="(val, index) in info.service_babel"
                 v-bind:key="index"
                 :id="val.id">{{val.name}}</div>
          </div>
        </div>
      </div>
      <div class="new">
        <span>性格要求：</span>
        <div class="bao">
          <div class="native">
            <div v-for="(val, index) in info.nature"
                 v-bind:key="index"
                 :id="val.id">{{val.name}}</div>
          </div>
        </div>
      </div>
      <div style="display: flex;">
        <label for>备注信息：</label>
        <el-input disabled
                  style="flex: 1;"
                  type="textarea"
                  rows="8"
                  resize="none"
                  maxlength="500"
                  v-model="info.remark"></el-input>
      </div>
      <div style="text-align: center; margin-top: 10px;"
           v-if="info.status !== 'complete'">
        <!--<el-button @click="cancelAction">返回</el-button>-->
        <el-button type="primary"
                   :disabled="info.status === 'release'"
                   @click="editAction">修改</el-button>
        <el-button @click="shelvesAction">{{ buttonTitle }}</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { needsInfo, getNeedsStatus } from '../../../service/Management'
import completeImage from '../../../assets/images/complete.png'
import notCompleteImage from '../../../assets/images/notComplete.png'

export default {
  mounted () {
    this.requestInfo()
  },
  data () {
    return {
      info: {}
    }
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['需求管理', '需求列表', '需求详情'])
  },
  computed: {
    address () {
      var text = ''
      if (this.info.addr) {
        if (this.info.addr.addr) {
          text += this.info.addr.addr
        }
      }
      return text
    },
    status () {
      if (this.info.status === 'soldout') {
        // 下架状态
        return '未成单'
      } else if (this.info.status === 'release') {
        // 发布状态
        return '未成单'
      } else {
        return '已成单'
      }
    },
    statusImg () {
      if (this.info.status === 'soldout') {
        // 下架状态
        return notCompleteImage
      } else if (this.info.status === 'release') {
        // 发布状态
        return notCompleteImage
      } else {
        return completeImage
      }
    },
    buttonTitle () {
      if (this.info.status === 'soldout') {
        // 下架状态
        return '上架'
      } else if (this.info.status === 'release') {
        // 发布状态
        return '下架'
      } else {
        return '-'
      }
    },
    region () {
      let region = []
      region = this.info.region
      return region ? region.length === 0 ? [{ id: '-1', code: -1, name: '不限' }] : region : []
    }
  },
  methods: {
    cancelAction () {
      this.$router.push('/demandRecords')
    },
    requestInfo () {
      const id = this.$route.params.id
      needsInfo(id).then(res => {
        console.log(res)
        this.info = res.data
      })
    },
    shelvesDemand () {
      const id = this.$route.params.id
      const params = `?id=${id}`
      getNeedsStatus(params).then(res => {
        this.$router.back(-1)
      })
    },
    editAction () {
      const id = this.$route.params.id
      this.$router.push(`/demandEdit/${id}`)
    },
    shelvesAction () {
      this.$confirm(`是否${this.buttonTitle}该需求`, '提示', {
        confirmButtonText: '是',
        cancelButtonText: '否',
        type: 'warning',
        center: true
      }).then(() => {
        this.shelvesDemand(this)
      }).catch(() => {
      })
    }
  }
}
</script>

<style scoped>
#top_view,
#middle_view,
#bottom_view {
  text-align: left;
  padding: 0px 15px;
}
.new {
  padding-bottom: 40px;
  text-align: left;
}
.new .bao {
  display: inline-block;
  width: 90%;
}
.el-col label,
.el-col span {
  line-height: 40px;
}
.new .bao .native,
.nature,
.skillList {
  display: flex;
  flex-wrap: wrap;
}
.new .bao .native div,
.new .bao .nature div,
.new .bao .skillList div {
  /* width: px; */
  padding: 3px 10px;
  height: 28px;
  cursor: pointer;
  margin-right: 12px;
  margin-top: 16px;
  border: 1px solid #f0f0ee;
  border-radius: 17px;
  line-height: 28px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.active {
  border-color: #1cb394 !important;
  color: #1cb394;
}
</style>



